<template lang="html">
  <div class="hd-sider-nav">
    <div v-for="(item, index) in list" :key="index" class="hd-sider-nav-item">
      <div class="hd-sider-nav-parent" @click="open(index)">
        <p class="hd-sider-nav-parent--title" v-text="item.text"></p>
        <svg-down v-if="index !== openedIndex" size="0.3"></svg-down>
        <svg-up v-else size="0.3"></svg-up>
      </div>
      <div v-if="item.children && item.children" v-show="index === openedIndex" class="hd-sider-nav-children">
        <p v-for="(child, i) in item.children" class="hd-sider-nav-children--text" v-text="child.text"></p>
      </div>
    </div>
  </div>
</template>

<script>
import SvgDown from 'components/svg/down.vue'
import SvgUp from 'components/svg/up.vue'

export default {
  name: 'hd-sider-nav',
  components: {
    SvgDown,
    SvgUp
  },
  props: {
    list: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      openedIndex: -1
    }
  },
  methods: {
    open (index) {
      if (index === this.openedIndex) {
        this.openedIndex = -1
      } else {
        this.openedIndex = index
      }
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b sider-nav {
    font-size: 32px;
    color: #666;
    line-height: 2.4;
    text-indent: 16px;

    @e item {
      border-bottom: 1px solid #d9d9d9;
    }

    @e parent {
      color: #333;
      display: flex;
      align-items: center;
      padding-right: 16px;
      border-bottom: 1px solid #d9d9d9;

      @m title {
        flex-grow: 1;

        @when active {
          color: #03a9f4;
        }
      }
    }

    @e children {
      padding-left: 32px;
      background-color: red;
      overflow: hidden;

      @m text {
        border-bottom: 1px solid #d9d9d9;

        @when active {
          color: #03a9f4;
        }
      }
    }
  }
}
</style>



